import React, { useState } from 'react';
import dayjs from 'utils/utc';

import axios from 'utils/axios'
import Page from "../../components/Page";
import ListPage from 'components/ListPage'
import { getColumnSearchProps } from 'utils/columnSearch';
import {Link} from "react-router-dom";

export default (props) => {
  const [dataSource, setDataSource] = useState({total: 0, data: []});

  const fetchData = async params => {
    let resp = await axios.get('/security/flagLog', {params});
    if (resp.code !== 0) return ;

    const data = resp.data;
    const lastLoginMap = {};
    if (data.lastLogin && data.lastLogin.length > 0) {
      data.lastLogin.map(item => {
        lastLoginMap[item.uid] = item.time;
      })
    }

    const dataList = [];
    if (data.log) {
      data.log.map(item => {
        const tmp = item.log;
        tmp.reg_time = item.user.reg_time;
        tmp.uname = item.user.uname;
        tmp.last_login = lastLoginMap[item.user["uid"]];
        dataList.push(tmp);
      });
    }

    // console.log("ddddd", resp);
    setDataSource({total: data.total, data: dataList})
  };

  const columns = [
    { title: '序号', dataIndex: 'key' },
    { title: 'UID', dataIndex: 'uid', key: 'uid', ...getColumnSearchProps('uid'),
      render: user => <Link to={`/user/userInfo?uid=${user}`}>{user}</Link>
    },
    { title: '昵称', key: 'uname', dataIndex: 'uname'},
    { title: '标记时间', key: 'create_time', dataIndex: 'create_time', render: time => dayjs.unix(time).utc().utcOffset(330).format('YYYY-MM-DD HH:mm:ss')},
    { title: '注册时间', key: 'reg_time', dataIndex: 'reg_time', render: time => dayjs.unix(time).utc().utcOffset(330).format('YYYY-MM-DD HH:mm:ss')},
    { title: '最后登录时间', key: 'last_login', dataIndex: 'last_login', render: time => dayjs.unix(time).utc().utcOffset(330).format('YYYY-MM-DD HH:mm:ss')},
    { title: '标记原因', key: 'reason', dataIndex: 'reason',
      render: reason => {
        const flagMap = {
          1: "连续弃牌",
          2: "连续诈胡",
          3: "连续吃牌",
          4: "连续赢",
        };
        return flagMap[reason];
      }
    },
    { title: '关联UID', key: 'with_uid', dataIndex: 'with_uid',
      render: with_uid => <Link to={`/user/userInfo?uid=${with_uid}`}>{with_uid}</Link>
    },
  ];

  return <Page title="异常用户名单">
    <ListPage
      columns={columns}
      fetchApi={fetchData}
      data={dataSource.data}
      total={dataSource.total}
      pageSize={20}
      rangeSearch={{showTime: true}}
      showTotal={(total, range) => `第${range[0]}-${range[1]}条, 共${total}条数据`}
    />
  </Page>;
}
